import React from 'react';
import { Link } from 'react-router-dom';

import Goodfirms from '../../assets/awards/goodfirms.png'
import designrush from '../../assets/awards/design-rush.png'
import clutch from '../../assets/awards/clutch.jpeg'

import { createGlobalStyle } from 'styled-components';

const GlobalStyles = createGlobalStyle`
.award-box {
    height: 150px;
    display: block;
  text-align: center;
  border: solid 1px #dddddd;
  padding: 20px 0 20px 0;
  text-decoration: none;
 background-color:#fff;
  

}


.award-box.style-2 {
  border: none;
  background: #ffffff;
}



.award-img {
    max-width:200px
}
@media (max-width: 544px) {  
    .award-img {
    max-width:150px
}
}
`




const catgor = () => (
    <>
        <GlobalStyles />
        <div className='row d-flex justify-content-center'>
            <div className="col-md-4 col-sm-8 col-10 mb-3">
                <div className=" style-2 award-box rounded d-flex align-items-center justify-content-center" to="">
                    {/* <i className="fa-brands fa-meta"></i> */}
                    <img className='award-img' src={Goodfirms} />
                    {/* <span>Web3.0</span> */}
                </div>
            </div>
            <div className="col-md-4 col-sm-8 col-10 mb-3">
                <div className=" style-2 award-box rounded d-flex align-items-center justify-content-center" to="">
                    {/* <i className="fa-brands fa-meta"></i> */}
                    <img className='award-img' src={designrush} />
                    {/* <span>Metaverse</span> */}
                </div>
            </div>
            <div className="col-md-4 col-sm-8 col-10 mb-3">
                <div className=" style-2 award-box rounded d-flex align-items-center justify-content-center" to="">
                    {/* <i className="fa-brands fa-meta"></i> */}
                    <img className='award-img' src={clutch} />
                    {/* <span>Metaverse</span> */}
                </div>
            </div>

        </div>
    </>
);
export default catgor;